@import './style/mixins.scss';
* {
  box-sizing: border-box;
}

html,
body,
button,
li,
ul,
h1,
p {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}
button {
  border: 0;
  background: none;
  font-size: 100%;
  font-size: 100%;
  text-transform: capitalize;
  vertical-align: baseline;
  font-family: inherit;
  font-weight: inherit;
  color: inherit;
}

body {
  max-width: 550px;
  margin: 0 auto;
  font-weight: 300;
  @media screen and (max-width: 570px){
    max-width: 97%;
  }
}
:focus {
  outline: none;
}